@import '../../utilities/color-palete.scss';

.resume {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 80px;

  .button-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px 0px 15px;

    .button {
      margin: 5px;
      padding: 10px 15px;
      color: $purple;;
      background-color: $light-blue;
      border: 1px solid $purple;
      border-radius: 8px;

      &:hover {
        color: $light-blue;
        background-color: $blue;
        border: 1px solid $light-blue;
      }
    }
  }

  .resumeImage {
    display: none;
  }
  
  .page-container {
    display: flex;
    width: 8.5in;
    height: 10.5in;
    border: 1px solid darkgray;
    padding: 0.25in;

    .left-column {
      margin-left: 15px;
      width: 30%;

      .profile-picture {
        display: flex;
        margin: auto;
        margin-bottom: 25px;
        object-fit: cover;
        width: 150px;
        border-radius: 20px;
      }
    }
    
    .middle-decoration {
      width: 5px;
      margin: 0px 8px;
      clip-path: polygon(50% 5%, 100% 50%, 50% 100%, 0% 50%);
      background-color: $blue;
    }
    
    .right-column {
      margin-right: 15px;
  
      .project-image {
        display: none;
      }
    }    
  }

  @media screen and (max-width: 880px) {
    .resumeImage {
      display: block;
      width: 90vw;
      border: 1px solid black;
    }

    .page-container {
      display: none;
    }
  }

}

@media print {
  .navigation-bar {
    display: none;
  }

  .resume {
    margin: 0px;

    .button-container {
      display: none;
    }
  
    .button {
      display: none;
    }
  
    .page-container {
      border: none;
    }
  }
}